@import url('https://fonts.googleapis.com/css2?family=Lato&family=Montserrat&display=swap');

* {
	margin: 0;
	padding: 0;
	box-sizing: border-box;
}

body {
	font-family: 'Montserrat', sans-serif;
}

.alert {
	width: fit-content;
	position: fixed;
	top: 48px;
	left: 50%;
	transform: translate(-50%, -50%);
}

header {
	display: flex;
	justify-content: space-between;
	height: 50px;
	align-items: center;
	margin: 0 120px;
}

.logo,
.header-nav,
.contact-btn {
	flex: 33.3%;
}

.contact-btn {
	display: flex;
	justify-content: flex-end;
}

.header-nav {
	display: flex;
	justify-content: center;
}

.header-nav ul li {
	list-style: none;
}

.title {
	display: flex;
	justify-content: space-between;
	align-items: center;
	margin: 0 120px;
	height: 550px;
}

.title-heading {
	margin-bottom: 24px;
}

.title-heading h1 {
	font-size: 48px;
}

.title-paragraph p {
	margin-bottom: 16px;
}

.profile-img {
	width: 400px;
	border-radius: 16px;
}

.about {
	margin: 0 120px;
	height: 450px;
}

.about p {
	margin: 0 auto;
	text-align: justify;
	line-height: 1.5;
	width: 450px;
}

.section-title {
	position: relative;
	text-align: center;
	margin-bottom: 48px;
}

.section-title h2 {
	font-size: 32px;
	position: relative;
	padding-bottom: 16px;
}

.section-title:before {
	width: 40px;
	height: 2px;
	content: '';
	position: absolute;
	bottom: 0;
	left: 50%;
	background-color: #ef4b6c;
	transform: translateX(-50%);
}

.education-wrapper {
	display: flex;
	justify-content: center;
	gap: 16px;
	margin: 0 120px;
	height: 450px;
}

.education-icon {
	display: flex;
	justify-content: center;
}

.container {
	background: #232931;
	width: 400px;
	height: 400px;
	margin: 0 auto;
	position: relative;
	box-shadow: 2px 5px 20px rgba(119, 119, 119, 0.5);
	border-radius: 24px;
}

.rightbox {
	padding: 0em 34rem 0em 0em;
	height: 100%;
}

.rb-container {
	font-family: 'PT Sans', sans-serif;
	width: 50%;
	margin: auto;
	display: block;
	position: relative;
}

.rb-container ul.rb {
	margin: 2.5em 0;
	padding: 0;
	display: inline-block;
}

.rb-container ul.rb li {
	list-style: none;
	margin: auto;
	margin-left: 10em;
	min-height: 50px;
	border-left: 1px dashed #fff;
	padding: 0 0 50px 30px;
	position: relative;
}

.rb-container ul.rb li:last-child {
	border-left: 0;
}

.rb-container ul.rb li::before {
	position: absolute;
	left: -18px;
	top: -5px;
	content: ' ';
	border: 8px solid rgba(255, 255, 255, 1);
	border-radius: 500%;
	background: #50d890;
	height: 20px;
	width: 20px;
	transition: all 500ms ease-in-out;
}

.rb-container ul.rb li:hover::before {
	border-color: #232931;
	transition: all 1000ms ease-in-out;
}

ul.rb li .timestamp {
	color: #50d890;
	position: relative;
	width: 100px;
	font-size: 12px;
}

.item-title {
	color: #fff;
}

.container-3 {
	width: 5em;
	vertical-align: right;
	white-space: nowrap;
	position: absolute;
}

.container-3 input#search {
	width: 150px;
	height: 30px;
	background: #fbfbfb;
	border: none;
	font-size: 10pt;
	color: #262626;
	-webkit-border-radius: 5px;
	-moz-border-radius: 5px;
	border-radius: 5px;
	margin: 0.9em 0 0 28.5em;
	box-shadow: 3px 3px 15px rgba(119, 119, 119, 0.5);
}

.container-3 .icon {
	margin: 1.3em 3em 0 31.5em;
	position: absolute;
	width: 150px;
	height: 30px;
	z-index: 1;
	color: #4f5b66;
}

input::placeholder {
	padding: 5em 5em 1em 1em;
	color: #50d890;
}
